<template>
  <div>
    <h3 class="title is-3">All Badges ({{ totalBadges }} Total)
    </h3>
    <button class="button is-primary"
            v-if="isAdmin && !displayAddBadge"
            @click="toggleAdd()">
      Add a Badge
    </button>
    <badge-create @close="toggleAdd()" v-show="displayAddBadge"></badge-create>
    <div class="badge-list">
      <badge v-for="badge in badges" :badge="badge" :key="badge.id"></badge>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import Badge from './Badge.vue';
import BadgeCreate from './Create.vue';

export default {
  data() {
    return {
      displayAddBadge: false
    };
  },
  computed: {
    ...mapGetters([
      'isAdmin',
      'badges'
    ]),
    totalBadges() {
      return this.badges.length;
    }
  },
  methods: {
    toggleAdd() {
      this.displayAddBadge = !this.displayAddBadge;
    }
  },
  components: {
    'badge': Badge,
    'badge-create': BadgeCreate
  },
  mounted() {
    this.$store.dispatch('getBadges');
  }
}
</script>

<style lang="scss">
.badge-list {
  margin-top: 2em;
}
</style>


