<template>
  <tr>
    <td>{{ user.fullname }}</td>
    <td>{{ user.role | titleCase }}</td>
    <td>{{ user.email }}</td>
    <td>
      <ul class="details-list">
        <li v-for="(value, key) in user.details" :key="value">
          <b>{{ key | titleCase}}:</b> {{ value | titleCase}}
        </li>
      </ul>
    </td>
  </tr>
</template>

<script>
export default {
  props: {
    user: {
      type: Object,
      required: true
    }
  }
}
</script>

<style lang="scss" scoped>
  .details-list {
    list-style: none;
    padding-left: 0;
  }
</style>


